<template lang="html">
	<div class="">
		<div class="max">
			<div class="header_nav qing">
				<div>
					消费明细/<span>说明说明说明说明说明说明</span>
				</div>
				<div>
					<div>数据总汇</div>
					<div>EXCEL导出</div>
					<a href="#">返回上一页></a>
				</div>
			</div>
			<div class="contain">
				<div class="form qing">
					<ul class="qing">
						<li>
							<span>关联信息</span>
							<el-input v-model="input" placeholder="姓名、车牌号、手机号" class="input"></el-input>
						</li>
						<li>
							<span>时间</span>
							<el-time-picker v-model="value2" picker-options="{selectableRange: '18:30:00 - 20:30:00'}" placeholder="任意时间点" id="input"></el-time-picker>
						</li>
						<li>
							<span>开票状态</span>
							<el-select v-model="value" placeholder="请选择" class="input">
								<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
						</li>
						<li>
							<span>扣费类型</span>
							<el-select v-model="value1" placeholder="请选择" class="input">
								<el-option v-for="item in options1" :key="item.value1" :label="item.label" :value="item.value1">
								</el-option>
							</el-select>
						</li>
					</ul>
					<button class="cx">查询</button>
				</div>
				<div class="line"></div>
				<div class="lb">
					<el-table ref="multipleTable" :data="tableData3" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
						<el-table-column type="selection" width="55">
						</el-table-column>
						<el-table-column label="时间">
							<template scope="scope"><p style="color: #2E52B3;">{{ scope.row.date }}</p></template>
						</el-table-column>
						<el-table-column  label="关联信息（运单/手机/人）" >
							<template scope="scope"><p style="color: #2E52B3;">{{ scope.row.phone }}</p></template>
						</el-table-column>
						<el-table-column  label="扣费类型" show-overflow-tooltip>
							<template scope="scope">{{ scope.row.type}}</template>
						</el-table-column>
						<el-table-column  label="收入" show-overflow-tooltip>
							<template scope="scope"><p style="color: red;">{{ scope.row.get }}</p></template>
						</el-table-column>
						<el-table-column  label="支出" show-overflow-tooltip>
							<template scope="scope">{{ scope.row.give }}</template>
						</el-table-column>
						<el-table-column  label="结余" show-overflow-tooltip>
							<template scope="scope">{{ scope.row.lost }}</template>
						</el-table-column>
					</el-table>
					<p class="gn">操作功能：<button>申请开票</button></p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				value2: new Date(2016, 9, 10, 18, 40),
				options: [{
					value: '选项1',
					label: '未开'
				}, {
					value: '选项2',
					label: '已开'
				}, {
					value: '选项3',
					label: '正在开'
				}],
				value: '',
				options1: [{
					value1: '选项1',
					label: '普通型'
				}, {
					value1: '选项2',
					label: '特殊型'
				}, {
					value1: '选项3',
					label: '变态型'
				}],
				value1: '',
				tableData3: [{
          date: '2016-05-03 20：00',
          phone: '19832321311',
          type:'定位',
          get:'',
          give:'0.2',
          lost:'5'
        },{
          date: '2016-05-03 20：00',
          phone: '19832321311',
          type:'短信',
          get:'',
          give:'0.2',
          lost:'0.2'
        },{
          date: '2016-05-03 20：00',
          phone: '19832321311',
          type:'充值',
          get:'100',
          give:'',
          lost:''
        },{
          date: '2016-05-03 20：00',
          phone: '19832321311',
          type:'业务信息费',
          get:'',
          give:'0.2',
          lost:'0.1'
        },],
        multipleSelection: []
			}
		}
	}
</script>

<style lang="less" scoped>
	.qing:after {
		display: block;
		clear: both;
		content: '';
	}

	.line {
		border: 1px solid #E6E9F0;
	}

	.max {
		width: 1600px;
		padding: 0px 30px;
		background: #FAFBFF;
		border: 1px solid #E6E9F0;
	}

	.header_nav {
		padding: 23px 0px;
		>div:nth-child(1) {
			float: left;
			color: #2E509A;
			font-size: 20px;
			font-weight: bolder;
			>span {
				font-size: 14px;
				color: #C6D3F3;
				margin-left: 12px;
				font-weight: normal;
			}
		}
		>div:nth-child(2) {
			float: right;
			>div {
				width: 100px;
				height: 40px;
				color: #417640;
				border: 1px solid #417640;
				line-height: 40px;
				text-align: center;
				float: left;
				border-radius: 5px;
				margin-left: 10px;
			}
			>a {
				float: left;
				margin-left: 15px;
				margin-top: 10px;
			}
		}
	}

	.contain {
		background: white;
		border: 1px solid #E6E9F0;
		padding: 40px 26px 30px 26px;
	}

	.form {
		>ul {
			>li {
				float: left;
				margin-left: 30px;
				>span {
					font-size: 16px;
					color: #3052AC;
					font-weight: bold;
					margin-right: 10px;
				}
			}
		}
	}

	.cx {
		width: 226px;
		height: 40px;
		outline: none;
		border: none;
		background: #2E52B3;
		cursor: pointer;
		font-size: 16px;
		color: white;
		font-weight: bold;
		margin: 18px 405px;
	}

	.input {
		width: 226px;
		height: 40px;
	}

	#input {
		width: 226px;
		height: 40px;
	}

	.lb {
		margin-top: 18px;
	}

	.gn {
		margin-top: 18px;
		color: #C6D3F3;
		>button {
			font-size: 12px;
			background: #65CC9F;
			color: white;
			width: 100px;
			height: 30px;
			border: none;
			outline: none;
			cursor: pointer;
		}
	}
</style>



